.programm {
  padding-top: 27px;
  padding-bottom: 32px;

  font-family: $base_font_regular;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: $color-special-dark-grey;


  @include tablet {
    font-size: 20px;
    line-height: 30px;
    padding-top: 65px;
    padding-bottom: 72px;
  }

  @include desktop {
    padding-top: 73px;
    padding-bottom: 82px;
  }

  input[type="text"],
  input[type="email"],
  input[type="tel"],
  textarea {
    width: 100%;
    padding: 10px 20px 12px;
    border: 1px solid $color-form-input;
    border-radius: 0;
    resize: vertical;
    box-sizing: border-box;

    font-family: $base_font_regular;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: $color-special-dark-grey;

    &:hover {
      border-color: $color-special_grey;
    }

    &:focus {
      border-color: $color-special_dark-grey;
      outline: none;
    }

    &:invalid:focus {
      border-color: $color-status-error;
    }

    @include tablet {
      font-size: 20px;
      line-height: 30px;
    }
  }

  label {
    padding-right: 10px;
    white-space: nowrap;
  }
}

.programm__header {
  margin: 0 0 37px;
  font-family: $base_font_regular;
  font-weight: normal;
  font-size: 36px;
  line-height: 1;
  text-transform: none;
  color: $color-basic-black;

  @include tablet {
    margin-bottom: 54px;
    font-size: 60px;
  }

  @include desktop {
    margin-bottom: 56px;
  }
}

.programm__slogan {
  margin: 0 0 39px;
  font-family: $base_font_regular;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.3;

  text-transform: uppercase;
  color: $color-basic-black;

  @include tablet {
    margin-bottom: 66px;
    padding: 0 40px;
    font-size: 20px;
    line-height: 1.5;
  }

  @include desktop {
    padding: 0;
  }
}

.programm__data {
  display: flex;
  flex-direction: column;
  padding-bottom: 26px;

  @include tablet {
    padding: 0 40px 71px;
  }

  @include desktop {
    flex-direction: row;
    padding: 0 0 69px;
  }
}

.programm__personal-list {
  margin: 0;
  padding: 0 0 27px;
  border-bottom: 1px solid $color-line;

  list-style-type: none;

  @include tablet {
    border-bottom: none;
  }

  @include desktop {
    flex-grow: 1;
    margin-right: 40px;
  }
}

.programm__personal-item {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 13px;

  &:last-child {
    margin-bottom: 0;
  }

  @include tablet {
    margin-bottom: 20px;
  }
}

.programm__aims-list {
  margin: 0;
  padding: 20px 0 22px;
  border-bottom: 1px solid $color-line;

  list-style-type: none;

  @include tablet {
    padding: 25px 20px 25px 28px;
    border: 1px solid #e7e7e7;
  }

  @include desktop {
    flex-grow: 1;
    margin-left: 40px;
  }
}

.programm__aims-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  margin-bottom: 13px;

  &:last-child {
    margin-bottom: 0;
  }
}

.programm__radio {
  display: block;
  position: relative;
  padding: 7px 10px 7px 45px;
  cursor: pointer;
  user-select: none;

  & input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }

  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 30px;
    width: 30px;
    background-color: white;
    border-radius: 50%;
    border: 1px solid $color-special-grey;
  }

  &:hover input ~ .checkmark::after {
    display: block;
    opacity: 0.3;
  }

  & input:checked ~ .checkmark {
    background-color: white;
  }

  & input:checked ~ .checkmark::after {
    display: block;
    opacity: 1;
  }

  & .checkmark::after {
    content: "";
    position: absolute;
    display: none;
    top: 8px;
    left: 8px;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: $color-basic-green;
  }

  @include tablet {
    padding: 5px 20px 5px 60px;

    .checkmark {
      height: 40px;
      width: 40px;
    }

    & .checkmark::after {
      top: 11px;
      left: 11px;
      width: 18px;
      height: 18px;
    }
  }
}

.programm__title {
  margin: 0 0 34px;
  font-weight: normal;
  font-size: 24px;
  line-height: 26px;

  text-transform: none;
  color: $color-basic-black;

  &.programm__title--adds {
    @include tablet {
      margin-bottom: 57px;
    }
  }

  @include tablet {
    margin-bottom: 76px;
    padding: 0 40px;
    font-size: 36px;
    line-height: 36px;
  }

  @include desktop {
    padding: 0;
  }
}

.programm__contact-list {
  margin: 0;
  padding: 0 0 27px;

  list-style-type: none;

  @include tablet {
    padding: 0 40px 65px;
  }

  @include desktop {
    padding: 0 0 69px;
  }
}

.programm__contact-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;

  &:last-child {
    margin-bottom: 0;
  }
}

.programm__comment {
  padding-bottom: 27px;

  textarea {
    min-height: 150px;

    @include tablet {
      min-height: 174px;
    }
  }

  @include tablet {
    padding: 0 40px 65px;
  }

  @include desktop {
    padding: 0 0 65px;
  }
}

.programm__adds-list {
  margin: 0;
  display: flex;
  flex-direction: column;
  padding: 0 0 22px;

  list-style-type: none;

  @include tablet {
    flex-direction: row;
    flex-wrap: wrap;
    padding-right: 40px;
    padding-left: 40px;
  }

  @include desktop {
    padding-right: 0;
    padding-left: 0;
    padding-bottom: 29px;
  }
}

.programm__adds-item {
  margin-bottom: 12px;

  @include tablet {
    flex-basis: 50%;
    flex-grow: 1;
    margin-bottom: 32px;
  }

  @include desktop {
    flex-basis: 25%;
  }
}

.programm__footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;

  @include tablet {
    flex-wrap: nowrap;
    padding: 0 40px;
  }

  @include desktop {
    padding: 0;
  }

  p {
    width: 50%;
    flex-grow: 1;
    align-self: center;
    margin-top: 22px;
    margin-bottom: 0;

    font-family: "Arial", sans-serif;
    font-weight: normal;
    font-size: 16px;
    line-height: 1;
    color: $color-basic-black;
    text-transform: none;

    @include tablet {
      margin-top: 0;
      margin-left: 30px;
    }

    @include desktop {
      margin-left: 40px;
    }
  }
}

.programm_btn {
  width: 100%;

  @include tablet {
    width: 50%;
    flex-grow: 1;
    margin-right: 30px;
  }

  @include desktop {
    margin-right: 40px;
  }
}
