.page-header {
  @include mobile-only {
    border-bottom: 1px solid $color-special-grey-2;
  }
}

.page-header__bg {
  position: absolute;
  top: var(--menu-height, 262px);
  width: 100%;
  text-align: center;
  z-index: -1;

  background-color: #65a93c;

  @include tablet {
    display: none;
  }

  @include desktop {
    display: inline-block;
    top: 0;
    right: 0;
    width: 50%;
    text-align: right;

    img {
      width: 720px;
      height: 696px;
    }
  }
}

.page-header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  padding-top: 11px;

  @include tablet {
    flex-wrap: nowrap;
    padding-top: 24px;
    justify-content: space-between;
    align-items: flex-end;
  }

  @include desktop {
    padding-top: 53px;
  }
}

.page-header__logo {
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @include mobile-only {
    margin-bottom: 17px;
    height: 38px;
    width: 190px;
    flex-basis: 240px;
    flex-grow: 1;
  }

  @include tablet {
    height: 52px;
    width: 174px;
  }

  @include desktop {
    height: 59px;
    width: 202px;
  }
}
